import React from 'react';
import { FaFacebookSquare, FaInstagram, FaTwitter } from 'react-icons/fa';

const Contact = () => {
    return (
        <>
        <div className="text-white p-5">
                <div className="container text-center">
                    <h1 className="display-4 mb-5">Contact</h1>
                </div>
            </div>
            <div className="row">
                <div className="col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-1">
                    <div id="formulaire" className="card">

                        <div  className="card-body">

                            <form  className="row g-3 needs-validation" >
                                <div className="col-md-12">
                                    <label htmlFor="societe" className="form-label">Société</label>
                                    <input type="text" className="form-control" id="societe" name="societe" />

                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="nom" className="form-label">Nom</label>
                                    <input type="text" className="form-control" id="nom" name="nom" />

                                </div>

                                <div className="col-md-6">
                                    <label htmlFor="prenom" className="form-label">Prénom</label>
                                    <input type="text" className="form-control" id="prenom" name="prenom" />
                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="tel" className="form-label">téléphone</label>
                                    <input type="text" className="form-control" id="tel" name="tel" />

                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="prix" className="form-label">Mail</label>
                                    <input type="number" className="form-control" id="prix" name="prix" />
                                </div>
                                <div className="form-group">
                                    <label for="exampleFormControlTextarea1">Commentaire</label>
                                    <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                                </div>

                                <div className="col-12">
                                    <button className="btn btn-secondary col-6" type="submit">envoyer</button>
                                </div>
                            </form>
                    
                        </div>
                        
                    </div>
                   
                </div>
                <div id="infosSte" className="col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-2 pt-5 border border-white rounded">
                    <ul className="list-group list-group-flush">
                        <li className="champ list-group-item">eCo² Game Store S.A</li>
                        <li className="champ list-group-item">ecoGamesSystem@eco.com</li>
                        <li className="champ list-group-item">(+33) 1.25.45.87.32</li>
                        <li className="champ list-group-item">182 Avenue du Maine 75014 Paris France</li>
                        <li className="bg-dark text-white text-center list-group-item"><FaTwitter className="icone"/> <FaFacebookSquare className="icone"/> <FaInstagram className="icone"/></li>
                    </ul>
                    </div>
            </div>
            <div className="col-12">
            <iframe id="carte" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1313.1974075338878!2d2.325305366774271!3d48.83160736751188!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47e671b40139aaed%3A0x4fd6e73111363f74!2s182-186%20Avenue%20du%20Maine%2C%2075014%20Paris!5e0!3m2!1sfr!2sfr!4v1613127370872!5m2!1sfr!2sfr"  frameborder="0"allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
            </div>
        </>
    );
}

export default Contact;