import React from 'react';
import { button } from 'react-dom';
import { FaCheckCircle, FaTimesCircle } from 'react-icons/fa';


const Liste = (props) => {

    return ( 
        <>
            <table id="tableauAdm" className="table">
                <thead className="thead-dark text-center ">
                    <tr>
                        <th>Id</th><th>Titre</th><th>Editeur</th><th>date de sortie</th><th>prix</th><th>image</th><th>disponibilité</th><th>Actions</th>
                    </tr>
                </thead>
                <tbody className="text-center">

                    {props.jeux.map((jeu, index)=>{
                        
                            return(
                                <tr key={index}>
                                    <td>{jeu.id}</td>
                                    <td>{jeu.titre}</td>
                                    <td>{jeu.editeur}</td>
                                    <td>{jeu.date}</td>
                                    <td>{jeu.prix} €</td>
                                    <td>
                                        <img src={`${jeu.image}`} width="55" />
                                        {/* <img src={`${process.env.PUBLIC_URL + '/images/'+jeu.image}`} width="55" /> */}
                                    </td>
                                    
                                    <td>{
                                        (jeu.disponible)
                                        ?<FaCheckCircle className="mt-3 text-success"/>
                                        :<FaTimesCircle className="mt-3 text-danger"/>
                                        }
                                    </td>
                                    <td>
                                    <button id="editeur" data-toggle="modal" data-target="#editModal" className="btn btn-success" 
                                    onClick={() =>{props.editJeu(index)}}>Editer</button>

                                    <button id="suppresseur" className="btn btn-danger " onClick={()=>{
                                        if(window.confirm('Etes vous sûr de vouloir supprimer cette référence ?'))
                                        {props.deleteJeu(index)};
                                        }}>Supprimer
                                    </button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </>

     );
}
 
export default Liste;