import React, { Component } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import { Link } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';

class Ajout extends Component {
    constructor(props) {
        super(props);
        
        this.initState = { 
            titre:"",
            editeur:"",
            date:"",
            prix:0,
            image:"",
            disponible:false
           
         }

         this.state = this.initState;
    }
        // modifier = (e) =>{
        //     const {name, value} = e.target;
        //     this.setState({[name]:value});
        // }
        // envoyer = (event) =>{
        //     event.preventDefault();
        //     console.log(this.state);
            
        //     this.props.ajouter(this.state);

        
    handleChange = (e)=>{
       
        const {name, type, checked, value} = e.target;
        const val = type === 'checkbox' ? checked : value;
        this.setState({[name]:val});

    }

    handleSubmit = (e)=>{
        e.preventDefault();
        let img = '';
        if(this.state.image){

            img = this.state.image.match(/[a-zA-Z0-9-_]+\.(jpg|png)/)[0];
        }
        const newGame = {
            titre: this.state.titre,
            editeur: this.state.editeur,
            date: this.state.date,
            prix: this.state.prix,
            image: img,
            disponible: this.state.disponible
        }
        this.props.ajouter(newGame);

        this.setState(this.initState);
        toast('Voiture ajoutée avec succès!', {className:'bg-success text-white fw-bolder'});
        window.location.reload(true);

    }


        render() {
               const {titre, editeur, date, prix, disponible} = this.state;
       
                return (

            <>
                   <div className="row">
                    <div className=" col-6 offset-3">
                    <div className="bg-dark text-white border card">
                        <div className="card-header text-center"> Formulaire d'ajout de jeux</div>
                        <div className="card-body">
                        
                            <form className="row g-3 needs-validation" onSubmit={this.handleSubmit}>
                            <div className="col-md-6">
                                <label htmlFor="titre" className="form-label">titre</label>
                                <input type="text" className="form-control"  id="titre" name="titre" value={titre} onChange={this.handleChange}  placeholder="Entrer le titre" />
                               
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="editeur" className="form-label">editeur</label>
                                <input type="text" className="form-control" id="editeur"  name="editeur" value={editeur} onChange={this.handleChange} placeholder="Entrer l'editeur'" />
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="date" className="form-label">date</label>
                                <input type="date" className="form-control" id="date" name="date" value={date} onChange={this.handleChange} placeholder="Entrer la date" />
                               
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="prix" className="form-label">Prix</label>
                                <input type="number" className="form-control" id="prix" value={prix} onChange={this.handleChange}  name="prix" placeholder="Entrer le prix" />
                            </div>
                            <div className="col-md-12">
                                <label htmlFor="image" className="form-label">Image</label>
                                <input type="file" className="form-control" id="image" onChange={this.handleChange} name="image"  />
                            </div>
                            
                            <div className="col-12">
                                <div className="form-check">
                                <input className="form-check-input" type="checkbox" id="disponible" onChange={this.handleChange} name="disponible" />
                                <label className="form-check-label" htmlFor="disponible">
                                    Disponible
                                </label>
                                
                                </div>
                            </div>
                            <div className="col-12">
                                <button className="btn btn-success col-12" type="submit">Soumettre</button>
                            </div>
                            </form>
                        </div>
                    </div>
                    <Link to="/admin123/reload"></Link>
                    </div>
                </div>
                <ToastContainer/>
            </>
        
          );
    }
}
 
export default Ajout;



