import React from 'react';
import { FaLeaf } from 'react-icons/fa';
import { IoLogoGameControllerB, IoMdHeart } from "react-icons/io";

const Presentation = () => {
    return ( 
        <>
         
        <div className="text-white container">
            <div className="row mt-5">
                <div classname="col-12">
                    <h1 className="text-center mb-5 mt-2 display-4">Ensemble, reprenons racine</h1>
                    
                    <div id="leaf" className="text-center col-md-5 col-sm-6 col-7"><FaLeaf/></div>
                    <p  className="text text-justify col-md-5 col-sm-6 col-7">Alors que le confinement nous a fait prendre conscience que nous vivions hors sol, Il est désormais temps de nous reconnecter à nos racines et de revenir aux fondements de nos vies. Les forêts font ainsi partie des éléments essentiels à nos existences et doivent être préservées et restaurées à grande échelle</p>
                </div>

            </div>
            <div className="row mt-5">
            <p  className="text text-justify col-md-5 col-sm-6 col-8 offset-sm-6 offset-md-6 offset-3">Nous n'héritons pas de la terre de nos parents, nous l'empruntons à nos enfants.
            <p id="ital">Antoine de Saint-Exupéry</p></p>
            </div>

            <div classname="col-12">
                    
            <div id="control" className="text-center col-md-5 col-sm-6 col-7 offset-md-3 offset-sm-2 offset-3 "><IoLogoGameControllerB/></div>
             <p className="text text-justify col-md-5 col-sm-6 col-7 offset-md-3 offset-sm-2 offset-3">Passionné de jeux vidéos et amoureux de la planète, ce projet fou est né en 2019 dans l'esprit d'une personne soucieuse de l'écologie. eCo² Game Store a pour ambition de planter des arbres! Et oui, pour chaque jeu acheté et téléchargé sur notre plateforme, 1€ est consacré à l'association "Coeur de Fôrets". Cette association agit activement à la reforestation à travers le monde. On ne peut pas nier que malgrès tous nos efforts, les serveurs informatiques rejettent énormément de cO2 alors pourquoi ne pas compenser un peu notre pollution avec une belle action pour notre planète Terre... </p>
            </div>

            <div className="row mt-5">
                <p  className="text text-right col-md-5 col-sm-7 col-8 offset-sm-5 offset-md-7 offset-3">Les petits ruisseaux font les grandes rivières...
                <p id="ital">Ovide</p></p>
            </div>

            <div className="col-12">
            <div id="coeur" className="text-center col-md-5 col-sm-6 col-7 offset-sm-1 offset-md-1 offset-1"><IoMdHeart/></div>
            <p  className="text text-justify col-md-5 col-sm-6 col-7 offset-sm-1 offset-md-1 offset-1">Alors n'hésitez plus, parce qu'acheter un jeu chez nous c'est voter pour un monde plus vert, parce qu'il est temps d'agir avant qu'il soit trop tard, parce qu'on a qu'une vie et qu'il est important de se faire plaisir... Cliquez et téléchargez le ou les titres de votre choix. Pour qu'il y en ait pour tous les goûts nous avons choisi chez eCo² de vous proposer aussi bien des jeux dernière génération que des titres " retro gaming ". On va de l'avant mais on n'oublie pas tous les jeux de notre enfance qui nous on fait vibrer à l'époque!</p>
            
            </div>

            <div className="row">
            <p  className="text textder text-justify col-md-5 col-sm-6 col-7 offset-sm-5 offset-md-6 offset-4">A très vite sur eCo² Game Store ! </p>
            
            </div>
        </div>
        </>
     );
}
 
export default Presentation;

