import React from 'react';
import { BrowserRouter, Redirect, NavLink, Route, Switch }  from 'react-router-dom';

import Admin from './Admin';
import Login from './Login';
import Logout from './Logout';
import Home from './pages/Home';
import Contact from './pages/Contact';
import Presentation from './pages/Presentation';
import Error from './pages/Error';
import Magasin from './pages/Magasin';
import Footer from './Footer';



const authenticate = ()=>{
    const d = new Date();
    const test = d.toLocaleDateString();
    if(test === sessionStorage.getItem('token')){
       return true; 
    }else{
        return false;
    }
}


const Menu = () => {
    return ( 
        <>
        
        <BrowserRouter basename="/eCo2">
        <header>
            
                <div className="container-fluid">
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <NavLink className="navbar-brand" to="/">
                    <img src="https://img2.freepng.fr/20180411/eoq/kisspng-leaf-plant-logo-root-plant-5ace75f3a9ada5.454160361523480051695.jpg" id="logo" alt="logo de l'entreprise eCo² Game Store, cercle blanc orné d'une feuille" width="60" height="60" className="align-middle" />
                   eCo² Game Store </NavLink>
                   <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button> 
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul id="navig" className=" navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink className="nav-link" activeClassName="active"  aria-current="page" to="/"  exact>Accueil</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" activeClassName="active" to="/magasin">Magasin</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" activeClassName="active" to="/presentation">Présentation</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" activeClassName="active" to="/contact">Contact</NavLink>
                            </li>
                        </ul>
                        <span className="navbar-text">
                        {!authenticate()
                        ?<NavLink className="nav-link" activeClassName="active" to="/login"> Connexion</NavLink >
                        :<NavLink className="nav-link" activeClassName="active" to="/logout"> Déconnexion</NavLink >
                        }
                        </span>
                        <form className="d-flex">
                            <input className="input form-control me-3" type="search" placeholder="Rechercher un jeu..." aria-label="Search"></input>
                            <button className="btn btn-outline-success" type="submit">Search</button>
                        </form>
                    </div>
                    </nav>
                </div>
            
        </header>
        <main>
            <Switch>
                <Route path='/' component={Home} exact/>
                <Route path='/magasin' component={Magasin} />
                <Route path='/presentation' component={Presentation} />
                <Route path='/contact' component={Contact} />
                <Route path='/login' component={Login} />
                <Route path='/logout' component={Logout} />
                <Redirect  from='/admin123/reload' to="/admin123" />
                <Route path='/admin123' render={()=>(
                    authenticate()
                    ?(<Admin />)
                    :(<Redirect to="/login"/>)
                )}  />
                <Route path='*' component={Error} />
            </Switch>
        </main>
       
        </BrowserRouter>
        <footer>
        <Footer/>
        </footer>
        </>

     );
}
 
export default Menu;


