import React from 'react';
import { FaRegCheckCircle, FaRegTimesCircle } from 'react-icons/fa';
import { button } from 'react-dom';

const Magasin = () => {

    const jeux = JSON.parse(localStorage.getItem('jeuxVideos'));

    return ( 
        <>

          <div className="text-white p-5">
                <div className="container text-center">
                    <h1 className="display-4">eCo² Game Store</h1>
                    <p className="lead"> Un jeu acheté, un arbre planté ! </p>
                </div>
            </div>
            <div id="contMag" className="row row-cols-2 row-cols-md-2 g-4 mt-1">
            {jeux.map((jeu, index) =>{
               return <div id="card" className="card mb-4" >
                    <div className="col-sm-12 row no-gutters">
                        <div className=" col-md-4">
                        <img className="mt-2" src={`${jeu.image}`} width="150" />
                        </div>
                        <div className="col-md-8">
                        <div className="card-body">
                            <h5 className="card-title">{jeu.titre}</h5>
                            <p className="card-text">date de sortie: {jeu.date}</p>
                            <p className="card-text">prix de vente: {jeu.prix} €</p>
                            <p className="card-text">{
                                (jeu.disponible)
                                ?<button class="btn border border-success"><FaRegCheckCircle className="text-success" size="20"/> commander</button>
                                :<p><FaRegTimesCircle className="text-danger" size="20"/> rupture</p>
                                }</p>
                            
                        </div>
                        </div>
                    </div>
                </div>
            })}
            </div>
        </>

     );
}
 
export default Magasin;