
import React, { Component } from 'react';
import Ajout from './Ajout';
import EditJeux from './EditJeux';
import Liste from './Liste';



class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            jeuxVideos:[
                {id:1, titre:'Everspace', editeur:'Rockfish Games', date:'25-05-2017', prix:30, image:'everspace.png', disponible:true},
                {id:2, titre:'Duke Nukem 3D', editeur:'Apogee Software', date:'29-03-1996', prix:5, image:'duke.jpg', disponible:true},
                {id:3, titre:'Fortnite', editeur:'Epic Gamess', date:'17-04-2017', prix:7, image:'fortnite.jpg', disponible:true},
                {id:4, titre:'Gears of War', editeur:'Microsoft Game Studio', date:'7-11-2006', prix:9, image:'gearsofwar4.jpg', disponible:false},
                {id:5, titre:'Control', editeur:'505 Games', date:'27-09-2019', prix:11, image:'control.jfif', disponible:true},
                {id:6, titre:'Yakuza', editeur:'Sega', date:'15-12-2006', prix:11, image:'yakuza.png', disponible:false},
                {id:7, titre:'Battlefield1', editeur:'Electronic Arts', date:'21-10-2016', prix:25, image:'battlefield1.jpg', disponible:true},
            ],
            editForm: false,
            indexJeu: -1,
            editJeu: {id:-1, titre:'', editeur:'', date:'', prix:0, image:'', disponible:false},
         }
    }

    componentDidMount =()=>{
        let tabJeux = JSON.parse(localStorage.getItem('jeuxVideos'));
        if(!tabJeux || tabJeux.length === 0){
            let jeux1 = localStorage.setItem('jeuxVideos', JSON.stringify(this.state.jeuxVideos));
            this.setState({jeuxVideos: jeux1});
        }else{
            let jeux2 = JSON.parse(localStorage.getItem('jeuxVideos'));
                this.setState({jeuxVideos: jeux2})
        }
    }

    removeJeu = (index)=>{
        const {jeuxVideos} = this.state;
        this.setState({
            jeuxVideos: jeuxVideos.filter((d, i)=>{
                return i!==index
            })
        }, function(){
            localStorage.setItem('jeuxVideos',JSON.stringify(this.state.jeuxVideos));
        })
    }

    handleShow = (etat)=>{
        this.setState({editForm:true});
    }

    itemJeu = (index)=>{
        const jEdit = this.state.jeuxVideos[index]; 
        this.setState({editJeu:jEdit, indexJeu: index});
        console.log(index);
     }

    // addJeu = (newJeu) => {
    //     console.log('ca marche')
    //     if(this.state.jeuxVideos.length !== 0){
    //         newJeu.id = (this.state.jeuxVideos[this.state.jeuxVideos.length - 1].id + 1)
    //     }
    //     this.setState({jeuxVideos: [...this.state.jeuxVideos, newJeu]},function(){
    //         localStorage.setItem('jeuxVideos',JSON.stringify(this.state.jeuxVideos));
    //     })
    // }

    addJeu = (game)=>{
        game.id = this.state.jeuxVideos[this.state.jeuxVideos.length -1].id +1;
        let jeuxClones = [...this.state.jeuxVideos, game];
        this.setState({jeuxVideos: jeuxClones}, ()=>{
            localStorage.setItem('jeuxVideos', JSON.stringify(this.state.jeuxVideos));
        });
        // console.log(game);
    }

    updateJeu = (jeuModif)=>{
        const games = [...this.state.jeuxVideos];
        games[this.state.indexJeu] = {...jeuModif};
        this.setState({jeuxVideos: games}, ()=>{
            localStorage.setItem('jeuxVideos',JSON.stringify(this.state.jeuxVideos));
        });
    }

    handleRaz = ()=>{
        localStorage.clear();
        window.location.reload(true);
    }

    render() {
        console.log(this.state.editJeu);
        
        return ( 
            <>  
                <h1 className="bg-secondary text-white text-center">Administration</h1>
               
                {/* <Ajout ajouter = {this.addJeu} />
                <EditJeux jeuShared = {this.state.editJeu} remplacer = {this.updateJeu}/>
                <Liste editJeu = {this.itemJeu} deleteJeu = {this.removeJeu} jeux = {this.state.jeuxVideos}/>
                 */}
                
                {
                    (!this.state.editForm && this.state.indexJeu === -1) 
                    &&  <button onClick={this.handleShow} className="btn btn-warning offset-11 mb-1 ">Ajouter
                       </button>
                }
                 <button className="btn btn-primary" onClick={this.handleRaz}>Vide mémoire</button>
                {
                    (!this.state.editForm && this.state.indexJeu > -1)
                    ?<EditJeux jeuShared = {this.state.editJeu} remplacer = {this.updateJeu} />
                    :
                    (this.state.editForm)
                    ?<Ajout ajouter = {this.addJeu} />
                    :<Liste editJeu = {this.itemJeu} deleteJeu = {this.removeJeu} jeux = {this.state.jeuxVideos} />
                } 
            </>

         );
    }
}

export default Admin;